import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { VhQuerySales } from 'ionic-vhframeworks';
import printJS from 'print-js';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

@Component({
  selector: 'app-print',
  templateUrl: './print.component.html',
  styleUrls: ['./print.component.scss']
})
export class PrintComponent implements OnInit {

  root: any;
  shipping_label_url: SafeUrl
  shipping_stamp_url: SafeUrl
  readyToPrint: boolean = false
  constructor(
    private router: Router,
    private vhComponent: VhComponent,
    private vhQuerySasles: VhQuerySales,
    private sanitizer: DomSanitizer
  ) {
    this.root = this.router.getCurrentNavigation().extras.state;
    

  }

  ngOnInit(): void {
    this.vhComponent.showLoading('', "transparent-loading").then(() => {
      this.vhQuerySasles.GetShippingStampForDropshipTiki(this.root.bill['code'], 'B6', this.root.info_Shop['access_token']).then((res: any) => {
        if (res.error) this.vhComponent.alertMessageDesktop('error', 'Đơn này chưa thể in')
        else {
          this.shipping_label_url = this.sanitizer.bypassSecurityTrustResourceUrl(res.url)
          this.readyToPrint = true
        }
        this.root.bill.currentDate = new Date()
        this.vhComponent.hideLoading(0)
      }).catch(err => { this.vhComponent.hideLoading(0); this.vhComponent.alertMessageDesktop('error', 'Đơn này chưa thể in') })
    })
  }
  goBack() {
    this.router.navigate([this.root.path], { state: this.root })
  }
}
