import JsBarcode from 'jsbarcode'
import PrintJs from 'print-js'
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { Component, Input, OnInit } from '@angular/core';
import QRCode from 'qrcode'
@Component({
  selector: 'app-k80',
  templateUrl: './k80.component.html',
  styleUrls: ['./k80.component.scss']
})
export class K80Component implements OnInit {
  @Input() bill:any;
  constructor(
    public vhAlgorithm:VhAlgorithm,
    public vhQuerySales:VhQuerySales
  ) { }

  ngOnInit(): void { 
  }

  ngAfterViewInit(): void {
    //Called after ngAfterContentInit when the component's view has been initialized. Applies to components only.
    //Add 'implements AfterViewInit' to the class.
    if(this.bill.code) {this.renderBarcode('#barcode-sort-code' , this.bill.code );this.renderQR()}
    if(this.bill.shipping.delivery_shipping_code) this.renderBarcode('#barcode-tracking-number' , this.bill.shipping.delivery_shipping_code )
  }
  renderBarcode(id,value) {
    JsBarcode(id, value, {
      format: "CODE128",
      lineColor: "#000",
      height: 40,
      displayValue: true, 
    });
  }

  renderQR(){
    QRCode.toCanvas(document.getElementById('canvas'), this.bill.code , function (error) {
      if (error) console.error(error)
      
    })
  }

  print(){
    PrintJs({
      printable: "sendo-k80",
      type: "html",
      scanStyles: false,
      style:
        'html { font-size: 9pt;}' +
        '@page { margin: 0 !important;}' + 
        '.k80 {max-width: 79mm;}'+
        '.k80 .content ' + 
        '.k80 .row{ width: 100%;padding: 0 8px; border-bottom: 1px solid rgb(19, 19, 19);}'+
        '.k80 .content #barcode-tracking-number{ width: 185px; max-width: 100%; }'+
        '.k80 .content .w50{  width: 50%; }'+
        '.k80 .content .w100{ width: 100%; }'+
        '.k80 .content .w33{  width: 33.33333%; }'+
        '.k80 .content .w90{  width: 90%; }'+
        '.k80 .content .text-center{  text-align: center; }'+
        '.k80 .content #barcode-sort-code{  width: 360px;  max-width: 90%; }'+
        '.k80 .content .center-all-content{  display: flex; align-items: center; justify-content: center; }'
    })
  }
}
