import JsBarcode from 'jsbarcode'
import PrintJs from 'print-js'
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { Component, Input, OnInit } from '@angular/core';
import QRCode from 'qrcode'
import { SafeUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-a5',
  templateUrl: './a5.component.html',
  styleUrls: ['./a5.component.scss']
})
export class A5Component implements OnInit {
  @Input() bill: any;
  @Input() shipping_label_url: SafeUrl = ''
  constructor(
    public vhAlgorithm: VhAlgorithm,
    public vhQuerySales: VhQuerySales
  ) { }

  ngOnInit(): void {
    
  }

  ngAfterViewChecked(): void {
    if (this.bill.code) this.renderBarcode('#barcode-sort-code', this.bill.code)
  }
  renderBarcode(id, value) {
    JsBarcode(id, value, {
      format: "CODE128",
      lineColor: "#000",
      height: 40,
      displayValue: true,
    });
  }

  renderQR() {
    QRCode.toCanvas(document.getElementById('canvas'), this.bill.code, function (error) {
      if (error) console.error(error)
    })
  }
  printA5() {
    PrintJs({
      printable: "tiki-a5",
      type: "html",
      scanStyles: false,
      style:
        '#shipping_stamp_url{padding: 0 2mm;margin-top:40px;font-size:9pt}' +
        '.shipping_stamp_url_content {border: 2px solid #000;font: 10pt monospace;color: #000;padding: 8px;width: 142mm;}' +
        '.receipt-header {display: inline-flex;}' +
        '.receipt-header img{width: 100px;}' +
        '.receipt-header-title {margin: auto 20px;line-height: 20px;}' +
        '.receipt-order-info {display: flex;justify-content: space-between;margin-top: 16px;}' +
        '.receipt-customer-info {display: inline-flex;margin-top: 16px;width: 100%;}' +
        '.receipt-customer-info .ant-table {border: 1px solid #000;border-radius: 0;margin: auto;width: 142mm;}' +
        '.receipt-customer-info .ant-table table {font-size: 8pt;}' +
        '.receipt-customer-info .header {width: 71mm;text-align: center;}' +
        '.receipt-item-info {display: inline-flex; margin-top: 16px;width: 100%;}' +
        '.receipt-item-info .ant-table {border: 1px solid #000;border-radius: 0;margin: auto;width: 142mm;padding: 0 4px;}' +
        '.receipt-item-info .td {border-bottom: none;padding: 0;}' +
        '.proof-of-delivery {display: inline-flex;margin-top: 16px;width: 100%;}' +
        '.proof-of-delivery .ant-table {border: 1px solid #000;border-radius: 0;margin: auto;width: 142mm;}'
    })
  }
  // print(){
  //   PrintJs({
  //     printable: "sendo-a5",
  //     type: "html",
  //     scanStyles: false,
  //     style:
  //       'html { font-size: 9pt;}' +
  //       '@page { margin: 0 !important;}' + 
  //       '.a5 {max-width: 148mm;}'+
  //       '.a5 .content ' + 
  //       '.a5 .content .row{ width: 100%;padding: 0 8px; border-bottom: 1px solid rgb(19, 19, 19);}'+
  //       '.a5 .content #barcode-tracking-number{ width: 185px; max-width: 100%; }'+
  //       '.a5 .content .w50{  width: 50%; }'+
  //       '.a5 .content .w100{ width: 100%; }'+
  //       '.a5 .content .w33{  width: 33.33333%; }'+
  //       '.a5 .content .w90{  width: 90%; }'+
  //       '.a5 .content .text-center{  text-align: center; }'+
  //       '.a5 .content #barcode-sort-code{  width: 360px;  max-width: 90%; }'+
  //       '.a5 .content .center-all-content{  display: flex; align-items: center; justify-content: center; }'
  //   })
  // }
}
